 .app__work{
    flex: 1;
    width: 100%;
    flex-direction: column;
 }

 .app__work-filter{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;

    margin: 3rem 0 1.5rem;

    .app__work-filter-item{
        padding: 0.6rem 1.2rem;
        border-radius: 0.5rem;
        background-color: #fff;
        color: #000;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;
        box-shadow: 0 0 20 rgba(0, 0, 0, 0.1);

        &:hover{
            background-color: var(--secondary-color);
            color: #fff;
        }
        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: 0.8rem;
        }
    }

    .item-active{
        background-color: var(--secondary-color);
            color: #fff;
    }
 }

 .app__work-portfolio{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item{
        width: 270px;
        flex-direction: column;
        margin: 1rem;
        padding: 1rem;
        background-color: #fff;
        border-radius: 0.5rem;
        color: #000;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover{
            box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
        }

        @media screen and (min-width: 2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }
        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }
 }

 .app__work-img{
    width: 100%;
    height: 230px;
    position: relative;

    img{
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        height: 350px;
    }
 }

 .app__work-hover{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;right: 0;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);

    border-radius: 0.5rem;
    opacity: 0;
    transition: all 0.2s ease;

    div{
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.5);
        color: #fff;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;

        svg{
            width: 50%;height: 50%;
            color: var(--white-color);
        }
    }
 }


 .app__work-content{
    width: 100%;
    position: relative;
    flex-direction: column;
    padding: 0.54rem;

    h4{
        margin-top: 1rem;
        line-height: 1.5;
    }
    .app__work-tag{
        position: absolute;
        padding: 0.5rem 1rem;
        background-color: #fff;
        top: -25px;
    }
 }